@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic&family=Noto+Sans+KR:wght@100..900&display=swap");

* {
	margin: 0;
	padding: 0;
	font-family: "Noto Sans KR", sans-serif;
	font-optical-sizing: auto;
	font-style: normal;
}

ul,
ol {
	list-style: none;
}

a {
	display: block;
	color: #333;
	text-decoration: none;
}

circle,
rect,
line {
	stroke-width: 20px;
	stroke-linecap: round;
	fill: transparent;
}

.animation-project {
	width: 100%;
	height: 100vh;
	margin: 0 auto;
	color: #fff;
	text-align: center;
	background: linear-gradient(180deg, #ff008c 0%, rgb(211, 9, 225) 100%);
}

.animation-project h1 {
	line-height: 100px;
	font-size: 40px;
	font-weight: 900;
}

.animation-project-bar {
	width: 370px;
	height: 0;
	border: 1px solid #fff;
	margin: 0 auto;
}

.animation-project-box {
	width: 1600px;
	height: auto;
	margin: 0 auto;
	position: relative;
	display: flex;
}

.box {
	width: 100px;
	height: 100px;
	background: #fff;
	position: absolute;
	top: 100px;
	left: 100px;
}

.box2 {
	background: white;
	border-radius: 20px;
	width: 100px;
	height: 100px;
	position: absolute;
	left: 300px;
	top: 100px;
}

.box3 {
	background: rgba(255, 255, 255, 0.2);
	border-radius: 20px;
	width: 100px;
	height: 100px;
	position: absolute;
	left: 500px;
	top: 100px;
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
	padding: 5px;
	box-sizing: border-box;
}

.box3 .item {
	background: white;
	border-radius: 50%;
	width: 35px;
	height: 35px;
}

.box4 {
	width: 100px;
	height: 100px;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 20px;
	position: relative;
	top: 100px;
	left: 700px;
}

.box4 .sub-box4 {
	width: 40px;
	height: 40px;
	border-radius: 20px;
	background-color: #fff;
	position: absolute;
}

.box5 {
	width: 100px;
	height: 100px;
	position: absolute;
	top: 100px;
	left: 900px;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 20px;
	overflow: hidden;
}

.box6 {
	width: 100px;
	height: 100px;
	position: absolute;
	top: 100px;
	left: 1100px;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 50px;
	overflow: hidden;
}

.box6 .number {
	color: white;
	font-size: 16px;
	border-radius: 1px solid black;
}
.box6 h3 {
	color: white;
	position: absolute;
	left: 30px;
	top: 30px;
	text-align: center;
}

.box7 {
	width: 100px;
	height: 100px;
	position: absolute;
	top: 100px;
	left: 1300px;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 50px;
	overflow: hidden;
}

.box7 h3 {
	width: 100%;
	height: 100%;
	line-height: 95px;
	text-align: center;
	font-size: 600;
}
.box8 {
	width: 100px;
	height: 100px;
	position: absolute;
	top: 100px;
	left: 1500px;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 20px;
	overflow: hidden;
}

.box8 .div1 {
	width: 80px;
	height: 10px;
	border-radius: 5px;
	position: absolute;
	background-color: #fff;
	top: 20px;
	left: 10px;
}

.box8 .div2 {
	width: 80px;
	height: 10px;
	border-radius: 5px;
	position: absolute;
	background-color: #fff;
	bottom: 20px;
	left: 10px;
}

.box9 {
	width: 100px;
	height: 100px;
	position: absolute;
	top: 300px;
	left: 100px;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 20px;
	overflow: hidden;
}

.box9 .circle1 {
	width: 40px;
	height: 40px;
	border-radius: 20px;
	background-color: #fff;
	position: absolute;
}
